:root {

	/* Z-index levels */
}

.root { }

.outputTooltip {
	left: 2.5em;
	position: absolute;
	top: 2.5em;
}

/* vertical slider */

.vertical .input,
.vertical .track {
	-webkit-transform-origin: 0 0;
	        transform-origin: 0 0;
	-webkit-transform: rotate(-90deg) translate(-100%, 0);
	        transform: rotate(-90deg) translate(-100%, 0);
}

/* disabled and readonly styles */

.disabled .track,
.readonly .track {
	background-color: #bbb;
}

/* required style, only applies to inputs with a label */

.required span::after {
	content: '*';
	color: red;
}

/* invalid style */

.invalid .track {
	border-color: red;
}

.invalid .thumb::before {
	background-color: red;
}

/* valid style */

.valid .inputWrapper::before {
	border-color: green;
}

.valid .inputWrapper::after {
	background-color: green;
}

.focused { }

.inputWrapper {

}

.focused input {
	outline: none;
}

.focused .thumb {
	-webkit-box-shadow: 0 0 7px 3px #34495e;
	        box-shadow: 0 0 7px 3px #34495e;
	border-color: white;
}

.track {
	background-color: #fff;
	border: 1px solid #424242;
	height: 0.75em;
}

.fill {
	background-color: #34495e;
}

.input {
	height: 0.75em;
}

.input::-ms-thumb {
	height: 2em;
	width: 2em;
}

.input::-moz-range-thumb {
	height: 2em;
	width: 2em;
}

.input::-webkit-slider-thumb {
	height: 2em;
	width: 2em;
}

.thumb {
	background-color: #fff;
	border: 1px solid #424242;
	height: 2em;
	left: 50%;
	margin-left: -1em;
	top: -0.625em;
	width: 2em;
}

.output { }
